.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all .3s ease;
    opacity: 0;
    visibility: hidden;
    display: flex;
    justify-content: center;
    padding: 20px 0;
    &.open {
        opacity: 1;
        visibility: visible;
    }
}
.p-close {
    svg {
        pointer-events: none;
    }
}
.layout {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 101;
    background: rgba(37, 42, 63, 0.4);

}
.popup-object {
    background: white;
    z-index: 102;
    display: none;
    height: fit-content;
    margin: auto;
    &.open {
        display: block;
    }
}
.auth-success {
    max-width: 872px;
    border-radius: 20px;
    width: calc(100% - 40px);
    padding: 88px 20px;
    @include max576 {
        padding: 40px 20px 52px;
    }
    &__inner {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        max-width: 460px;
        text-align: center;
        margin: 0 auto;
        .rounded {
            background: rgba(52, 168, 83, 0.1);
            width: 100px;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        p {
            color: $grey1;
        }
        h6 {
            margin: 27px 0 16px;
            @include max576 {
                margin-top: 39px;
            }
        }
        .auth-btn {
            margin: 32px 0 0;
            max-width: 340px;
            width: 100%;
            height: 44px;
            @include max576 {
                margin-top: 36px;
            }
        }
    }
}
.notifications-popup,
.filter-popup {
    max-width: 336px;
    width: 100%;
    background: #FFFFFF;
    border-radius: 12px;
    overflow: hidden;
    .title-row {
        padding: 18px 20.5px;
        border-bottom: 1.5px solid #F3F3F3;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .h2 {
            font-size: 18px;
        }
        .p-close {
            width: 38px;
            height: 38px;
            border: 1px solid #F3F3F3;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
        }
    }
    &__inner {
        padding: 22px 20.5px 36px;
        min-height: 686px;
        display: flex;
        flex-direction: column;
        ul {
            li {
                display: flex;
                align-items: center;
                margin-bottom: 18px;
                .img {
                    width: 44px;
                    height: 44px;
                    flex: 0 0 44px;
                    border-radius: 50%;
                    overflow: hidden;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                    margin-right: 12px;
                    &:not(.user) {
                        background: #F5F7FF;
                        display: flex;
                        align-items: center;    
                        justify-content: center;
                        img {
                            width: unset;
                            height: unset;
                            object-fit: contain;
                        }
                    }
                }
                p {
                    &:first-child {
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 130%;
                        color: #1B2124;
                        margin-bottom: 4px;
                    }
                    &:last-child {
                        font-size: 12px;
                        line-height: 160%;
                        color: #7F879E;
                    }
                }
            }
        }
        .btn {
            margin-top: auto;
            height: 46px;
            width: 100%;
        }
    }
    .filter {
        @include filter;
    }
}

.applications-popup {
    max-width: 800px;
    width: calc(100% - 40px);
    background: #FFFFFF;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    padding: 34px 30px 60px;
    @include max576 {
        padding: 24px 20px 34px;
    }
    .applications-close {
        @include closePopup;
    }
    .badge {
        @include badge(#268D61);
        margin-bottom: 24px;
        @include max768 {
            margin-bottom: 16px;
        }
    }
    .title {
        font-weight: 700;
        font-size: 22px;
        line-height: 130%;
        color: #1B2124;
        margin-bottom: 6px;
        @include max768 {
            font-size: 20px;
        }
        @include max576 {
            font-size: 16px;
        }
    }
    ul.info {
        margin-top: -6px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        li {
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #7F879E;
            margin-top: 6px;
            display: flex;
            align-items: center;
            p {
                margin-left: 8px;
                span:first-child {
                    font-weight: 600;
                    color: #1B2124;
                }
            }
            svg {
                &.info-alt {
                    cursor: pointer;
                    margin-left: 8px;
                }
            }
            &:not(:last-child) {
                margin-right: 20px;
            }
        }
    }
    ul.requirement {
        border: 1.5px solid #F3F3F3;
        border-radius: 12px;  
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        margin: 24px 0;
        font-weight: 400;
        font-size: 12px;
        line-height: 160%;
        overflow: hidden;
        color: #7F879E;
        @include max768 {
            grid-template-columns: 1fr 1fr;
        }
        @include max576 {
            grid-template-columns: 54% 46%;
        }
        p {
            &:not(:last-child) {
                margin-bottom: 5px;
            }
            &:last-child {
                display: flex;
                align-items: center;
                svg {
                    margin-right: 8px;
                    @include max576 {
                        margin-right: 4px;
                    }
                }
                span {
                    font-weight: 600;
                    color: #1B2124; 
                }
            }
        }
        li {
            padding: 0px 20px;
            height: 70px;
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include max768 {
                border: 1.5px solid #F3F3F3;
                padding: 0px 16px;
            }
            &:not(:last-child) {
                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    width: 1px;
                    height: 32px;
                    transform: translateY(-50%);
                    background: $lightgrey1;
                    right: 0;
                    @include max768 {
                        display: none;
                    }
                }
            }
        }
    }
    .description {
        margin-bottom: 40px;
        .h2 {
            margin-bottom: 12px;
            @include max576 {
                font-style: 14px;
            }
        }
        .text {
            font-weight: 400;
            font-size: 14px;
            line-height: 160%;
            color: #7F879E; 
            margin-bottom: 12px;
        }
        .link {
            cursor: pointer;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #1F8FFF;
            display: flex;
            align-items: center;
            justify-content: start;
            span {
                margin-right: 4px;
            }
        }
    }
    .applications-grid {
        &__inner {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px;
            @include max768 {
                grid-template-columns: 1fr
            }
        }
        .devs-item {
            @include devCard;
            background: #FFFFFF;
            box-shadow: 0px 4px 20px rgba(127, 135, 158, 0.15);
            border-radius: 12px;
            @include max576 {
                .pm {
                    font-size: 12px;
                }
                &__info {
                    ul {
                        margin-bottom: 16px;
                    }
                }
                .btn {
                    span span {
                        display: none;
                    }
                }
            }
        }
        .row {
            display: flex;
            align-items: center;
            margin-bottom: 18px;
            .p3 {
                margin-left: auto;
                color: #7F879E;
                margin-right: 12px;
                @include max576 {
                    display: none;
                }
            }
            .select-wrapper {
                @include customSelect;
                width: 144px;
                height: 44px;
                flex: 0 0 144px;
                background: #F3F3F3;
                @include max576 {
                    margin-left: auto;
                    width: 120px;
                    height: 32px;
                    flex: 0 0 120px;
                    font-size: 12px;
                }
            }
        }
    }
    .navigation {
        margin-top: 42px;
        display: flex;
        align-items: center;
        justify-content: center;
        * {
            cursor: pointer;
        }
        .arrow {
            &:hover path {
                stroke: #47D18C;
            }
        }
        .pages-nav {
            margin: 0 44px;
            display: grid;
            grid-template-columns: repeat(4, 44px);
            grid-gap: 12px;
            @include max768 {
                margin: 0 36px;
            }
            &__item {
                width: 44px;
                height: 44px;
                border-radius: 8px;
                background: #F9F9F9;
                font-weight: 600;
                font-size: 14px;
                line-height: 16px;
                color: #7F879E;
                transition: all .3s ease;
                display: flex;
                align-items: center;
                justify-content: center;
                &:hover {
                    color: #47D18C;
                }
                &.active {
                    background: #47D18C;
                    color: #FFFFFF;
                }
            }
        }
    }
}
.editapp-popup {
    max-width: 800px;
    width: calc(100% - 40px);
    background: #FFFFFF;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    padding: 34px 30px 47px;
    @include max576 {
        padding: 24px 20px 34px;
    }
    .h1 {
        margin-bottom: 50px;
        position: relative;
        @include max576 {
            margin-bottom: 45px;
            &::after {
                content: "";
                position: absolute;
                bottom: -24px;
                width: calc(100% + 40px);
                left: -20px;
                height: 1.5px;
                background: #F3F3F3;
            }
        }
    }
    form {
        &>*:not(:last-child) {
            margin-bottom: 40px;
            @include max992 {
                margin-bottom: 30px;
            }
            @include max768 {
                margin-bottom: 22px;
            }
        }
    }
    position: relative;
    .editapp-close {
        @include closePopup;
    }
    .input-group {
        @include inputGroup;
        .input-wrapper {
            input {
                height: 54px;
                &:not(:placeholder-shown) {
                    border: 2px solid transparent;
                }
                @include max576 {
                    height: 46px;
                }
            }
            .badge {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 12px;
                @include badge(#268D61);
                @include max576 {
                    display: none;
                }
            }
            &.no-icon {
                input {
                    padding-left: 12px;
                }
            }
        }
        .select-wrapper {
            position: relative;
            svg {
                &:last-child {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 18px;
                }
            }
            &.has-icon {
                select {
                    padding-left: 57px;
                    @include max576 {
                        padding-left: 47px;
                    }
                }
            }
    
            .select-icon {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 20px;
                @include max576 {
                    left: 11px;
                }
            }
            select {
                width: 100%;
                padding: 0 29px;
                border: none;
                height: 54px;
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                outline: none;
                background: #F6F8F9;
                border-radius: 12px;
                font-weight: 400;
                font-size: 14px;
                line-height: 160%;
                color: #1B2124;
                @include max1200 {
                    height: 50px;
                }

                @include max576 {
                    height: 46px;
                }
                option {
                    &[disabled] {
                        color: #AAAFBE;
                    }
                }

                &::-ms-expand {
                    display: none;
                }
            }
        }
    }
    .input-row {
        display: grid;
        grid-gap: 23px;
        grid-template-columns: 1fr 1fr;
        @include max768 {
            &.nowrap {
                grid-gap: 15px;
            }
            &:not(.nowrap) {
                grid-template-columns: 1fr;
                grid-gap: 22px;
            }
        }
        .input-group {
            margin-bottom: 0;
        }
    }
    .radio-group {
        display: flex;
        align-items: center;
        .radio-wrapper {
            display: flex;
            align-items: center;
            &:first-child {
                margin-right: 40px;
                @include max576 {
                    margin-right: 26px;
                }
            }
        }
        .radio-item {
            position: relative;
            width: 20px;
            height: 20px;
            margin-right: 14px;
            &:hover {
                .active {
                    border-color: #47D18C;
                }
            }
            input {
                position: absolute;
                top: 0;
                left: 0;
                margin: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
                z-index: 2;
                cursor: pointer;
            }
            .active {
                transition: all .3s ease;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
                opacity: 0.8;
                border: 1.5px solid #7F879E;
                border-radius: 50%;
            }
            input:checked + .active {
                background: #47D18C;
                border: 2px solid #47D18C;
                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    width: 6px;
                    height: 6px;
                    background: #FFFFFF;
                    border-radius: 50%;
                }
            }
            p {
                font-weight: 400;
                font-size: 14px;
                line-height: 160%;
                color: #1B2124;
            }
        }
    }
    .half-wrapper {
        max-width: 342px; 
        @include max768 {
            max-width: 100%;
        }
    }
    .tags-group {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-top: -6px;
        .tag {
            margin-top: 6px;
            background: #F9F9F9;
            border-radius: 12px;
            padding: 8px 14px;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #7F879E;
            margin-right: 9px;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            height: 32px;
            transition: all .3s ease;
            svg {
                margin-left: 8px;
                cursor: pointer;
            }
            &:hover {
                color: #3D425A;
                svg path {
                    stroke: #3D425A;
                }
            }
        }
        .add-btn {
            border: 1px solid #1F8FFF;
            border-radius: 12px;  
            width: 32px;
            height: 32px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            margin-top: 6px;
            .mobile-version {
                display: none;
            }
            @include max576 {
                border: none;
                svg {
                    display: none;
                    &.mobile-version {
                        display: block;
                    }
                }
            }
        }
    }
    .textarea-wrapper {
        textarea {
            background: #F6F8F9;
            border-radius: 12px;
            height: 150px;
            width: 100%;
            resize: none;
            padding: 12px 20px 12px;
			border: 2px solid transparent;
            outline: none;
			&:hover {
				background: $lightgrey1;
			}
			&:focus {
				background: $lightgrey3;
				border: 2px solid $green3;
			}
			&:active {
				background: white;
				border: 2px solid $grey3;
			}
            @include max576 {
                padding: 12px 13px 12px;
            }
        }
    }
    .btn-group {
        display: grid;
        grid-template-columns: 128px 128px;
        grid-gap: 12px;
        justify-content: end;
        @include max576 {
            grid-gap: 16px;
            grid-template-columns: 1fr;
            justify-content: unset;
            .secondary {
                grid-row: 1 / 2;
            }
            .neutral {
                grid-row: 2 / 3;
            }
        }
        .btn {
            height: 44px;
            font-size: 12px;
            line-height: 130%;
        }
    }
    .mobile-group {
        display: none;
        @include max576 {
            display: block;
        }
        .select-wrapper {
            select {
                padding: 0 12px;
            }
        }
    }
}
.project__details-popup {
    max-width: 800px;
    width: calc(100% - 40px);
    background: #FFFFFF;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    .title-row {
        padding: 18px 20.5px;
        border-bottom: 1.5px solid #F3F3F3;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .p-close {
            width: 38px;
            height: 38px;
            border: 1px solid #F3F3F3;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            @include max768 {
                width: 28px;
                height: 28px;
            }
        }
    }
    &__inner {
        padding: 20px 24px 24px;
        .date {
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            text-align: right;
            color: #7F879E;
            span {
                color: $setBlack1
            }
            @include max576 {
                text-align: left;
            }
        }
        .row__top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            .badge {
                @include badge(#268D61);
                width: 108px;
                @include max576 {
                    height: 28px;
                }
            }
        }
        .content {
            .h1 {
                margin-bottom: 16px;
                color: #1F8FFF;
                @include max576 {
                    margin-bottom: 8px;
                }
            }
            .date {
                text-align-last: left;
            }
            .block {
                margin: 30px 0;
                @include max576 {
                    margin: 20px 0;
                }
                p {
                    color: #7F879E; 
                    font-size: 16px;
                    line-height: 160%;
                }
                .h2 {
                    margin-bottom: 16px;
                    @include max576 {
                        margin-bottom: 8px;
                    }
                }
            }
        }
        .list {
            p {
                display: flex;
                align-items: center;
                @include max576 {
                    align-items: start;
                    span {
                        font-size: 14px;
                        line-height: 160%;
                    }
                }
                svg {
                    margin-right: 9px;
                    flex: 0 0 15px;
                    @include max576 {
                        margin-top: 3px;
                    }
                }
                &:not(:last-child) {
                    margin-bottom: 12px;
                }
            }
        }
        .dev-row {
            &>.p4 {
                @include max576 {
                    display: none;
                }
            }
            .mobp {
                display: none;
                font-weight: 600;
                font-size: 12px;
                line-height: 130%;
                color: #1B2124;
                margin-left: auto;
                @include max576 {
                    display: block;
                }
            }
            padding-bottom: 20px;
            display: flex;
            align-items: end;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                bottom: 0;
                width: 100%;
                left: 0;
                height: 1px;
                background: #D9D9D9;
            }
            @include max576 {
                align-items: center;
            }
            .img {
                width: 42px;
                height: 42px;   
                flex: 0 0 42px;
                margin-right: 12px;
                overflow: hidden;
                border-radius: 50%;
                @include max576 {
                    width: 34px;
                    height: 34px;
                    flex: 0 0 34px;
                }
            }
            .content {
                margin-right: 22px;
            }
            .h2 {
                @include max576 {
                    font-size: 12px;
                    line-height: 100%;
                    margin-bottom: 4px;
                }
            }
            p {
                color: #7F879E;
                span {
                    font-weight: 600;
                    color: #1B2124;
                }
                @include max576 {
                    font-size: 11px;
                    line-height: 100%;
                }
            }
        }
        .row__bottom {
            padding-top: 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include max768 {
                display: grid;
                grid-template-columns: 1fr;
                grid-gap: 20px;
            }
            .hour {
                display: flex;
                align-items: center;
                font-weight: 500;
                font-size: 12px;
                line-height: 130%;
                color: #7F879E;
                margin-right: 26px;
                @include max576 {
                    margin-right: 0;
                }
                svg {
                    margin-right: 10px;
                    @include max576 {
                        margin-right: 6px;
                    }
                }
                span {
                    color: #1B2124;
                    font-weight: 600;
                }
            }
            .left {
                display: flex;
                align-items: center;
                @include max576 {
                    justify-content: space-between;
                }
            }
            .btn {
                width: 141px;
                height: 40px;
                font-weight: 600;
                font-size: 14px;
                line-height: 20px;
            }
            .checkbox-item {
                @include checkbox;
                margin-left: auto;
                color: #7F879E;
            }
        }
    }

}