@function rem($px) {
	@return ($px / 16)+rem;
}
@mixin selectDefault {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
}

@mixin customSelect {
	width: 118px;
	flex: 0 0 118px;
	height: 100%;
	background: #fff;
	border-radius: 10px;
	font-weight: 500;
	font-size: 14px;
	line-height: 130%;
	color: #1B2124;
	position: relative;
	select {
		border: none;
		padding: 0 14px;
		background: transparent;
		height: 100%;
		width: 100%;
		@include selectDefault;
	}
	svg {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 14px;

	}
}

@mixin elementAction {
	display: grid;
	grid-template-columns: 36px 36px;
	grid-gap: 16px;
	.like {
		&.active,
		&:active {
			border: 1px solid #D4D7DF;
			svg path {
				fill: #3D425A;
				stroke: #3D425A;
			}
		}
	}
	.option {
		position: relative;
		&.active,
		&:active {
			background: #F6F8F9;
			border: 1px solid #D4D7DF;
			path {
				stroke: #3D425A;
			}
			
		}
		&.active {
			.option-content {
				opacity: 1;
				pointer-events: visible;
			}
		}
		&-content {
			opacity: 0;
			pointer-events: none;
			position: absolute;
			height: fit-content;
			width: 150px;
			background: #FFFFFF;
			border: 1px solid #F3F3F3;
			box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.1);
			border-radius: 10px;
			top: calc(100% + 5px);
			right: 0;
			padding: 14px;
			font-weight: 500;
			font-size: 12px;
			line-height: 130%;
			color: #7F879E;
			.blue {
				color: #1F8FFF;
			}
			li:not(:last-child) {
				margin-bottom: 12px;
			}
			
		}
	}
	&>* {
		border-radius: 50%;
		height: 36px;
		border: 1px solid #F3F3F3;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		transition: all .3s ease;
		path {
			transition: all .3s ease;
		}
		&:hover {
			border: 1px solid #AAAFBE;
			svg path {
				fill: #3D425A;
				stroke: #3D425A;
			}
		}
		&.edit {
			&:hover {
				svg path {
					fill: unset;
					stroke: #3D425A;
				}
			}
		}
	}
}

@mixin filter {

	&-item {
		&:not(:last-child) {
			margin-bottom: 26px;
		}
		&__title {
			display: flex;
			cursor: pointer;
			align-items: center;
			justify-content: space-between;
		}
		&__list {
			li {
				display: flex;
				cursor: pointer;
				align-items: center;
				justify-content: space-between;
				&.active {
					.value {
						color: #47D18C; 
					}
				}
				.value {
					background: #F3F3F3;
					border-radius: 10px;
					font-weight: 500;
					font-size: 12px;
					line-height: 130%;
					text-align: center;
					color: #7F879E;
					padding: 4px 10px;
				}
			}
		}
		&__flex {
			.tag {
				display: flex;
				flex-wrap: wrap;
				margin-top: -12px;
				&-item {
					margin-top: 12px;
					background: #F9F9F9;
					border-radius: 12px;
					padding: 8px 14px;
					font-weight: 500;
					font-size: 12px;
					cursor: pointer;
					line-height: 130%;
					width: fit-content;
					transition: all .3s ease;
					&.active {
						background: #BBE2C6; 
						color: #268D61;
					}
					&:not(:last-child) {
						margin-right: 8px;
					}
				}
			}
		}
		.filter-content {
			padding-top: 26px;
			display: none;
			&.active {
				display: block;
			}
		}
		.checkbox-item {
			@include checkbox;
			.checkbox {
				margin-right: 14px;
			}
			label {
				font-size: 14px;
				line-height: 160%;
				color: #7F879E;
			}
		}
	}
	li {
		&:not(:last-child) {
			margin-bottom: 16px;
		}
	}
}
@mixin devCard {
	background: #FFFFFF;
	border-radius: 12px;
	padding: 20px;
	@include max576 {
		padding: 21px 12px;
	}
	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 13px;
		.img {
			overflow: hidden;
			border-radius: 50%;
		}
	}
	&__action {
		@include elementAction;
	}
	&__info {
		color: #7F879E;
		.h2 {
			color: $setBlack1;
		}
		// .pm {
		// 	font-weight: 600;
		// }
		&>.pm {
			margin: 8px 0 14px;
		}
		ul {
			margin-bottom: 32px;
			display: flex;
			align-items: center;
			li {
				&.salary {
					span {
						&:first-child {
							color: #1B2124;
						}
					}
				}
				display: flex;
				align-items: center;
				svg {
					margin-right: 9px;
				}
				&:first-child {
					margin-right: 20px;
				}
			}
		}
		.btn-row {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-gap: 10px;
			.btn {
				height: 38px;
				font-size: 14px;
				svg {
					margin-right: 9px;
				}
			}
		}
	}
}
@mixin checkbox {
	display: flex;
	align-items: center;

	label {
		margin: 0;
	}
	.checkbox {
		width: 20px;
		height: 20px;
		border: 1.5px solid #7F879E;
		border-radius: 6px;
		position: relative;
		margin-right: 10px;
		transition: all .3s ease;

		&:hover {
			border: 1.5px solid #47D18C;
		}
	}

	.custom-checkbox {
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		margin: 0;
		z-index: 11;
		cursor: pointer;
	}
	.tick {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 10;
		opacity: 0;
		width: 22px;
		height: 22px;

		img {
			width: 100%;
			height: 100%;
		}
	}
	.custom-checkbox:checked+.tick {
		opacity: 1;
	}
}
@mixin closePopup {
	width: 36px;
	height: 36px;
	right: 30px;
	top: 30px;
	cursor: pointer;
	border-radius: 50%;
	position: absolute;
	z-index: 1;
	border: 1px solid #F3F3F3;
	display: flex;
	align-items: center;
	justify-content: center;
	@include max576 {
		top: 20px;
		right: 20px;
	}
}
@mixin badge($bg) {
	width: 90px;
	height: 32px;
	background: $bg;
	border-radius: 12px;
	font-weight: 600;
	font-size: 12px;
	line-height: 120%;
	display: flex;
	align-items: center;
	justify-content: center;
	color: white;
	svg {
		margin-right: 8px;
	}
}
@mixin inputGroup {
	margin-bottom: 25px;
	@include max576 {
		margin-bottom: 20px;
	}
	label {
		display: block;
		margin-bottom: 12px;
		color: $black1;
		font-weight: 600;
	}
	.input-wrapper {
		position: relative;
		.error-text {
			position: absolute;
			top: calc(100% + 4px);
			left: 0;
			color: $red1;
			opacity: 0;
			pointer-events: none;
		}
		&.error {
			input {
				border-color: $red3;
				background: white;
				color: $red1;
			}
			input + svg {
				path {
					stroke: $red1;
				}
			}
		}
		input {
			background: #F6F8F9;
			border-radius: 12px;
			border: none;
			width: 100%;
			height: 48px;
			color: $setBlack3;
			padding: 0 16px 0 48px;
			transition: all .3s ease;
			border: 2px solid transparent;
			@include max576 {
				height: 46px;
			}
			&:hover {
				background: $lightgrey1;
			}
			&:focus {
				background: $lightgrey3;
				border: 2px solid $green3;
			}
			&:active {
				background: white;
				border: 2px solid $grey3;
			}
			&:not(:placeholder-shown) {
				border: 2px solid $grey3;
			}
			@include placeholder {
				font-weight: 400;
				font-size: 14px;
				line-height: 160%;
				color: #7F879E;
			}
		}
		&>svg {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 16px;
		}
		.password_vision {
			left: unset;
			right: 16px;
			cursor: pointer;
		}
	}
}
@mixin placeholder {
	&::-webkit-input-placeholder {
		@content;
	}

	&:-moz-placeholder {
		@content;
	}

	&::-moz-placeholder {
		@content;
	}

	&:-ms-input-placeholder {
		@content;
	}
}

