.dashboard-wrapper {
    padding-right: 50px;
    display: grid;
    grid-template-columns: minmax(260px, 1fr) 5.5fr;
    grid-gap: 24px;
    background: linear-gradient(198.65deg, rgba(193, 227, 211, 0.52) 12.62%, rgba(245, 248, 252, 0.45) 41.35%), #FBF3E5;
    min-height: 100vh;
    @include max1200 {
        grid-gap: 15px;
        padding-right: 20px;
    }
    @include max992 {
        grid-template-columns: 1fr;
        padding-left: 20px;
    }
    @include max576 {
        background: linear-gradient(198.65deg, rgba(193, 227, 211, 0.52) 12.62%, rgba(245, 248, 252, 0.45) 41.35%), #FBF3E5;
    }
    .sidebar {
        background: #FFFFFF;
        border-right: 1px solid #F3F3F3;
        padding: 40px 16px 54px;
        display: flex;
        flex-direction: column;
        .row {
            display: flex;
            align-items: center;
            margin-bottom: 49px;
            @include max992 {
                margin-bottom: 70px;
            }
        }
        .burger {
            display: none;
            margin-right: 0px;
            @include max992 {
                display: block;
                margin-left: 4px;
            }
        }
        @include max992 {
            position: fixed;
            top: 0;
            left: 0;
            transform: translateX(-100%);
            width: 260px;
            transition: all .3s ease;
            z-index: 10;
            height: 100%;
            padding-top: 28px;
            &.active {
                transform: translateX(0);
            }
        }
        .logo {
            margin-left: 24px;
            @include max992 {
                margin-bottom: 8px;
                margin-left: 14px;
            }
            @include max576 {
                margin-bottom: 4px;
                img {
                    width: 98px;
                    height: auto;
                }
            }
        }
        &__inner {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: #7F879E;
            .title {
                height: 48px;
                padding: 0 20px;
                display: flex;
                align-items: center;
                justify-content: start;
                border-radius: 10px;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                background: #47D18C;
                color: #FFFFFF;
                margin-bottom: 16px;
                svg {
                    margin-right: 14px;
                }
            }
            &>ul {
                &>li {
                    &:not(:last-child) {
                        margin-bottom: 15px;
                    }
                    &.mb-item {
                        margin-bottom: 50px;
                    }
                    .sidebar-item {
                        cursor: pointer;
                        height: 48px;
                        display: flex;
                        align-items: center;
                        padding: 0 20px;
                        &.active {
                            border-radius: 10px;
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 130%;
                            background: #47D18C;
                            color: white;
                            .stroke path {
                                stroke: white;
                            }
                            .fill path {
                                fill: white;
                            }
                            svg:last-child path {
                                stroke: white;
                            }
                        }
                        svg {
                            margin-right: 12px;
                        }
                        svg:last-child {
                            margin-left: auto;
                        }
                        .active-value {
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 130%;
                            color: #47D18C;
                            padding: 0 10px;
                            margin-left: auto;
                        }
                    }
                }
            }
        }
        &-subitems {
            padding: 12px 0;
            display: none;
            li {
                padding-left: 56px;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
                &.active {
                    color: #1F8FFF;
                }
            }
        }
        .logout-btn {
            height: 48px;
            display: flex;
            align-items: center;
            padding: 0 20px;
            margin-top: auto;
            color: #F05817;
            svg {
                margin-right: 12px;
            }
        }
    }
    .header {
        margin: 40px 0;
        @include max992 {
            margin: 20px 0;
        }
        .row {
            display: flex;
            align-items: center;
            @include max992 {
                margin-bottom: 20px;
                &>.search-wrapper {
                    display: none;
                }
            }
        }
        .search-wrapper {
            height: 48px;
            background: #FFFFFF;
            border-radius: 12px;
            position: relative;
            padding: 4px;
            max-width: 384px;
            flex: 0 0 33%;
            display: flex;
            align-items: center;
            @include max992 {
                max-width: 100%;
            }
            @include max576 {
                justify-content: space-between;
            }
            &>svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 16px;
                @include max576 {
                    left: 10px;
                }
            }
            input {
                flex: 1;
                padding-left: 53px;
                @include max576 {
                    flex: unset;
                    width: 200px;
                    padding-left: 47px;
                }
            }
        }
        &>.search-wrapper {
            display: none;
            @include max992 {
                display: flex;
            }
        }
        .select-wrapper {
            width: 118px;
            flex: 0 0 118px;
            height: 100%;
            background: #EAEDEE;
            border-radius: 10px;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #1B2124;
            position: relative;
            @include max576 {
                width: 99px;
                flex: 0 0 99px;
                font-weight: 500;
                font-size: 12px;
                line-height: 130%;
            }
            select {
                border: none;
                padding: 0 14px;
                background: transparent;
                height: 100%;
                width: 100%;
                @include selectDefault;
                @include max576 {
                    padding: 0 10px;
                }
            }
            svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 14px;
                @include max576 {
                    right: 10px;
                }

            }
        }
        .notification,
        .settings,
        .user {
            width: 49px;
            height: 48px;
            flex: 0 0 48px;
            background: #FFFFFF;
            box-shadow: 0px 18px 54px rgba(0, 0, 0, 0.02);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            cursor: pointer;
            @include max576 {
                width: 38px;
                height: 38px;
                flex: 0 0 38px;
                svg {
                    width: 18px;
                    height: 18px;
                }
            }
        }
        .settings {
            margin-right: 28px;
            @include max576 {
                margin-right: 14px;
            }
        }
        .user {
            @include max576 {
                img {
                    width: 31px;
                    height: 31px;
                    overflow: hidden;
                    object-fit: cover;
                }
            }
        }
        .notification {
            margin-right: 28px;
            margin-left: auto;
            @include max576 {
                margin-right: 14px;
            }
            &::after {
                content: attr(data-count);
                position: absolute;
                width: 28px;
                height: 19px;
                right: -10px;
                top: 0;
                background: #FE6E66;
                border-radius: 100px;
                font-weight: 500;
                font-size: 12px;
                text-align: center;
                line-height: 18px;            
                color: #FFFFFF;
                @include max576 {
                    width: 25px;
                    font-size: 10px;
                    line-height: 15px;
                    height: 15px;
                }
            }
        }
        .header-logo {
            display: none;
            margin-bottom: 8px;
            @include max992 {
                display: block;
            }
            @include max576 {
                margin-bottom: 4px;
                img {
                    width: 98px;
                    height: auto;
                }
            }
        }
        
    }

    .content {
        &__inner {
            display: grid;
            grid-gap: 24px;
            grid-template-columns: 3fr 1.25fr;
            @include max1200 {
                grid-gap: 15px;
            }
            @include max768 {
                grid-template-columns: 1fr;
            }
            &-base>* {
                &:not(:last-child) {
                    margin-bottom: 40px;
                    @include max1200 {
                        margin-bottom: 20px;
                    }
                }
            }
        }
        &__top {
            @include max576 {
                height: 130px;
                position: relative;
                overflow-y: hidden;
                margin-right: -20px;
                width: calc(100% + 20px);
            }
            &-wrapper {
                display: grid;
                grid-template-columns: repeat(3,1fr);
                grid-gap: 24px;
                color: #7F879E;
                @include max1200 {
                    grid-gap: 15px;
                }
                @include max576 {
                    position: absolute;
                    top: 0;
                    left: 0;
                    grid-template-columns: repeat(3,224px);
                    grid-gap: 12px;
                    height: 100%;
                }
            }
            &-item {
                padding: 24px 22px;
                background: #FFFFFF;
                border-radius: 12px;
                @include max576 {
                    width: 224px;
                    height: 100%;
                }
            }
            .row {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 20px;
            }
            h3 {
                color: $setBlack1;
            }
        }
        &__chart {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 33px 24px;
            @include max576 {
                padding: 23px 15px;
                margin-bottom: 36px !important;
            }
            img {
                width: 100%;
            }
            ul {
                display: flex;
                align-items: center;
                margin-right: 45px;
                @include max1200 {
                    margin-right: 20px;
                }
                @include max576 {
                    display: none;
                }
                li {
                    display: flex;
                    align-items: center;
                    &:first-child {
                        margin-right: 16px;
                        span {
                            background: #1F8FFF;
                        }
                    }
                    &:last-child {
                        span {
                            background: #78C58C;
                        }
                    }
                    span {
                        display: block;
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;
                        margin-right: 8px;
                    }
                }
            }
            &>ul {
                display: none;
                @include max576 {
                    display: flex;
                    margin: 15px 0;
                }
            }
        }
        .title-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .chart-info {
                display: flex;
                align-items: center;
            }
            .chart-select {
                height: 32px;
                border: 1.5px solid #F3F3F3;
                border-radius: 8px;
                width: 73px;
                flex: 0 0 73px;
                position: relative;
                select {
                    padding: 0 12px;
                    @include selectDefault;
                    border: none;
                    background: transparent;
                    height: 100%;
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 130%;
                }
                svg {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 12px;
                }
            }
        }
        &__recommend {
            .view-link {
                display: flex;
                align-items: center;
                color: #1F8FFF;
                font-weight: 600;
                font-size: 14px;
                line-height: 22px;
                @include max576 {
                    font-size: 12px;
                }
                svg {
                    margin-left: 4px;
                    @include max576 {
                        width: 18px;
                        height: 18px;
                    }
                }
            }
            .title-row {
                margin-bottom: 24px;
                @include max1200 {
                    margin-bottom: 16px;
                }
            }
        }
        .devs-grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px 15px;
            @include max1400 {
                grid-template-columns: 1fr;
            }
            @include max576 {
                grid-gap: 12px;
            }
        }
        .devs-item {
            @include devCard;
        }
        &__inner-side {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 24px;
            height: fit-content;
            @include max768 {
                display: none;
            }
            .side-block {
                display: flex;
                flex-direction: column;
                position: relative;
                padding-bottom: 20px;
                &:not(:last-child) {
                    border-bottom: 1px solid #F3F3F3;
                    margin-bottom: 22px;
                }
                .edit {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 28px;
                    height: 28px;
                    border: 1px solid #F3F3F3;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    &:hover {
                        border: 1px solid #D4D7DF;
                        path {
                            stroke: #3D425A;
                        }
                    }
                }
                .h1 {
                    margin: 8px 0 20px;
                }
                .info {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    li {
                        &:first-child {
                            margin-right: 20px;
                        }
                        display: flex;
                        align-items: center;
                        svg {
                            margin-right: 8px;
                        }
                        span {
                            font-weight: 600;
                        }
                    }
                }
                .row {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                }
                p {
                    &.mt {
                        margin-top: 20px;
                    }
                    color: #7F879E;
                }
                .social {
                    margin-top: 20px;
                    color: #7F879E;
                    li {
                        &:not(:last-child) {
                            margin-bottom: 16px;
                        }
                        svg {
                            margin-right: 8px;
                        }
                        a {
                            display: flex;
                            align-items: center;
                        }
                    }
                }
            }
            .circle {
                margin: 0 auto 6px;
                --v:calc( ((18/5) * var(--p) - 90)*1deg);
                width:109px;
                height:109px;
                display:inline-block;
                border-radius:50%;
                padding:2px;
                position: relative;
                background:
                  linear-gradient(#FFFFFF,#FFFFFF) content-box,
                  linear-gradient(var(--v), $grey3     50%,transparent 0) 0/min(100%,(50 - var(--p))*100%),
                  linear-gradient(var(--v), transparent 50%,$blue1        0) 0/min(100%,(var(--p) - 50)*100%),
                  linear-gradient(to right, $grey3 50%,$blue1 0);
                  .img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    width: 80%;
                    height: 80%;
                    background: #F3F3F3;
                    padding: 5px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                  }
             }
              
              
              
              
        }
        &__row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 33px;
            @include max768 {
                display: grid;
                grid-template-columns: 1fr;
                grid-gap: 20px
            }
            @include max576 {
                margin-bottom: 20px;
            }
            .select-wrapper {
                @include customSelect;
                height: 44px;
                width: 144px;
                flex: 0 0 144px;
                font-weight: 600;
                @include max576 {
                    height: 42px;
                    font-size: 12px;
                }
            }
            p {
                color: #7F879E;
            }
            &-action {
                display: flex;
                align-items: center;
                &>*:not(:last-child) {
                    margin-right: 16px;
                }
                .p3 {
                    @include max576 {
                        display: none;
                    }
                }
                @include max576 {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    grid-gap: 20px;
                    .select-wrapper {
                        flex: unset;
                        width: unset;
                        margin-right: 0;
                    }
                }
            }
            &-title {
                display: flex;
                justify-content: space-between;
                .mobile-sort {
                    display: none;
                    width: 42px;
                    height: 42px;
                    justify-content: center;
                    align-items: center;
                    background: #FFFFFF;
                    border-radius: 12px;
                    cursor: pointer;
                    @include max768 {
                        display: flex;
                    }
                }
                h5 {
                    margin-bottom: 4px;
                }
            }
            .btn {
                height: 46px;
                padding: 0 24px;
                font-size: 14px;
                line-height: 130%;
                svg {
                    margin-left: 8px;
                }
                @include max576 {
                    height: 42px;
                    font-size: 12px;
                }
            }
        }
        .vacancy-grid,
        .timesheet-grid {
            display: grid;
            grid-template-columns: 1fr;
            grid-gap: 28px;
            @include max576 {
                grid-gap: 12px;
            }
        }
        .vacancy-item {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 20px 20px 36px;
            cursor: pointer;
            &__top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 24px;
                .badge {
                    line-height: 127%;
                }
            }
            &__action {
                @include elementAction;
            }
            &__inner {
                &>*:not(:last-child) {
                    margin-bottom: 12px;
                }
                ul {
                    margin-top: -6px;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    li {
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 130%;
                        color: #7F879E;
                        margin-top: 6px;
                        display: flex;
                        align-items: center;
                        p {
                            margin-left: 8px;
                            span:first-child {
                                font-weight: 600;
                                color: #1B2124;
                            }
                        }
                        svg {
                            &.info-alt {
                                cursor: pointer;
                                margin-left: 8px;
                            }
                        }
                        &:not(:last-child) {
                            margin-right: 20px;
                        }
                    }
                }
                .text {
                    color: #7F879E;
                    @include max576 {
                        font-size: 12px;
                    }
                }
            }
            &__bottom {
                margin-top: 36px;
                display: flex;
                align-items: end;
                justify-content: space-between;
                @include max576 {
                    margin-top: 22px;
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-gap: 20px;
                }
                .btn {
                    width: 156px;
                    height: 44px;
                    font-size: 12px;
                    line-height: 130%;
                    svg {
                        margin-right: 4px;
                    }
                }
                .date {
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 130%;
                    text-align: right;
                    color: #7F879E;
                    span {
                        color: $setBlack1
                    }
                    @include max576 {
                        text-align: left;
                    }
                }
                .h3 {
                    margin-bottom: 12px;
                }
                ul {
                    display: flex;
                    align-items: center;
                    li {
                        border: 1.5px solid #FFFFFF;
                        overflow: hidden;
                        border-radius: 50%;
                        width: 38px;
                        height: 38px;
                        justify-content: center;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        background: #F3F3F3;
                        font-weight: 400;
                        font-size: 12px;
                        line-height: 130%;
                        color: #1B2124;
                        
                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                        &:not(:first-child) {
                            margin-left: -8px;
                        }
                    }
                }
            }
        }
        .timesheet-item {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 20px 20px 36px;
            cursor: pointer;
            @include max576 {
                padding: 20px 20px 31px;
            }
            &__top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 24px;
                .badge {
                    line-height: 127%;
                    width: 108px;
                    @include max576 {
                        height: 28px;
                    }
                }
                .date {
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 130%;
                    text-align: right;
                    color: #7F879E;
                    span {
                        color: $setBlack1
                    }
                    @include max576 {
                        text-align: left;
                    }
                }
            }
            &__inner {
                &>*:not(:last-child) {
                    margin-bottom: 12px;
                }
                .h1 {
                    color: #1F8FFF;
                }
                ul {
                    margin-top: -6px;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    li {
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 130%;
                        color: #7F879E;
                        margin-top: 6px;
                        display: flex;
                        align-items: center;
                        p {
                            margin-left: 8px;
                            span:last-child {
                                font-weight: 600;
                                color: #1B2124;
                            }
                        }
                        svg {
                            &.info-alt {
                                cursor: pointer;
                                margin-left: 8px;
                            }
                        }
                        &:not(:last-child) {
                            margin-right: 20px;
                        }
                    }
                }
                .text {
                    color: #7F879E;
                    @include max576 {
                        font-size: 12px;
                    }
                }
            }
            &__bottom {
                .dev-row {
                    &>.p4 {
                        @include max576 {
                            display: none;
                        }
                    }
                    .mobp {
                        display: none;
                        font-weight: 600;
                        font-size: 12px;
                        line-height: 130%;
                        color: #1B2124;
                        margin-left: auto;
                        @include max576 {
                            display: block;
                        }
                    }
                    margin-top: 20px;
                    display: flex;
                    align-items: end;
                    @include max576 {
                        align-items: center;
                    }
                    .img {
                        width: 42px;
                        height: 42px;   
                        flex: 0 0 42px;
                        margin-right: 12px;
                        overflow: hidden;
                        border-radius: 50%;
                        @include max576 {
                            width: 34px;
                            height: 34px;
                            flex: 0 0 34px;
                        }
                    }
                    .content {
                        margin-right: 22px;
                    }
                    .h2 {
                        @include max576 {
                            font-size: 12px;
                            line-height: 100%;
                            margin-bottom: 4px;
                        }
                    }
                    p {
                        color: #7F879E;
                        span {
                            font-weight: 600;
                            color: #1B2124;
                        }
                        @include max576 {
                            font-size: 11px;
                            line-height: 100%;
                        }
                    }
                }
            }
        }
        .filter {
            @include filter;
        }
        .simple-block {
            background: #FFFFFF;
            border-radius: 12px;  
            padding: 28px 24px 36px;
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            &__inner {
                position: relative;
                .h1 {
                    margin-bottom: 28px;
                }
                table {
                    border-collapse: collapse;
                    td {
                        padding-bottom: 20px;
                        &:first-child {
                            vertical-align: top;
                            color: #AAAFBE;
                            width: 140px;
                        }
                        ul {
                            li {
                                &:not(:last-child) {
                                    margin-bottom: 12px;
                                }
                                a {
                                    display: flex;
                                    align-items: center;
                                    color: #7F879E;
                                    svg {
                                        margin-right: 8px;
                                    }
                                }
                            }
                        }
                    }
                }
                .text {
                    color: #7F879E;  
                    margin-bottom: 24px;
                }
                .list {
                    p {
                        display: flex;
                        align-items: center;
                        color: #7F879E;
                        &:not(:last-child) {
                            margin-bottom: 12px;
                        }
                        svg {
                            margin-right: 5px;
                        }
                    }
                }
            }
            .edit {
                position: absolute;
                top: 0;
                right: 0;
                width: 28px;
                height: 28px;
                border: 1px solid #F3F3F3;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                &:hover {
                    border: 1px solid #D4D7DF;
                    path {
                        stroke: #3D425A;
                    }
                }
            }
        }
    }
    
    .badge {
        @include badge(#268D61);
        &.orange {
            @include badge(#F59A74);
        }
    }
    .burger {
        display: none;
        margin-right: 15px;
        @include burger;
        @include max992 {
            display: block;
        }
    }


}